e;\n    bottom: -10%;\n    right: -10%;\n    width: 100%;\n    height: 60%;\n    background: rgba(139, 92, 246, 0.3);\n    filter: blur(60px);\n    border-radius: 50%;\n    opacity: 0.5;\n  }\n}\n\n.status-header {\n  position: relative;\n  height: 40px;\n  padding: 0 24px;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  z-index: 50;\n  font-size: 12px;\n  font-weight: 500;\n  color: rgba(255, 255, 255, 0.9);\n  user-select: none;\n}\n\n.header-left {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n}\n\n.back-button {\n  width: 28px;\n  height: 28px;\n  border-radius: 50%;\n  background: rgba(255, 255, 255, 0.1);\n  border: 1px solid rgba(255, 255, 255, 0.2);\n  color: white;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n  transition: all 0.2s;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.2);\n    transform: scale(1.1);\n  }\n\n  i {\n    font-size: 14px;\n  }\n}\n\n.close-header-button {\n  width: 28px;\n  height: 28px;\n  border-radius: 50%;\n  background: rgba(255, 255, 255, 0.1);\n  border: 1px solid rgba(255, 255, 255, 0.2);\n  color: white;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n  transition: all 0.2s;\n  margin-left: 8px;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.2);\n    transform: scale(1.1);\n  }\n\n  &:active {\n    transform: scale(0.95);\n  }\n\n  i {\n    font-size: 14px;\n  }\n}\n\n.header-time {\n  font-size: 12px;\n}\n\n.header-icons {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  font-size: 10px;\n\n  i {\n    color: rgba(255, 255, 255, 0.6);\n  }\n\n  .signal-text {\n    font-family: monospace;\n    opacity: 0.6;\n  }\n\n  .battery-indicator {\n    width: 24px;\n    height: 12px;\n    border-radius: 2px;\n    border: 1px solid rgba(255, 255, 255, 0.4);\n    position: relative;\n    margin-left: 4px;\n\n    .battery-fill {\n      position: absolute;\n      top: 2px;\n      bottom: