/**
 * 
 * -  > 10   10
 * - / > 500  
 */
async function enforcePotentialCapOnStartup() {
    try {
        //  Mvu 
        if (typeof Mvu === 'undefined' || !Mvu) {
            console.warn('[] Mvu ');
            return;
        }
        const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
        if (!mvuData || !mvuData.stat_data) {
            console.warn('[]  MVU ');
            return;
        }
        let hasChanges = false;
        let hasNegative = false;
        const warnings = [];
        // 1. 
        const rawPotential = get(mvuData.stat_data, '._', 0);
        const potential = Number(rawPotential);
        if (Number.isFinite(potential) && potential > 10) {
            console.warn(`[] ${potential} (> 10) 10`);
            warnings.push(`${potential}>10`);
            set(mvuData.stat_data, '._', 10);
            hasChanges = true;
        }
        // 2. 
        const rawAttrPoints = get(mvuData.stat_data, '.$', 0);
        const attrPoints = Number(rawAttrPoints);
        if (Number.isFinite(attrPoints) && attrPoints < 0) {
            console.warn(`[] ${attrPoints} 0`);
            warnings.push(`${attrPoints}`);
            set(mvuData.stat_data, '.$', 0);
            hasChanges = true;
            hasNegative = true;
        }
        if (Number.isFinite(attrPoints) && attrPoints > 500) {
            console.warn(`[] ${attrPoints} (> 500)`);
            warnings.push(`${attrPoints}>500`);
            set(mvuData.stat_data, '.$', 0);
            hasChanges = true;
        }
        // 3. 
        const rawSkillPoints = get(mvuData.stat_data, '.$', 0);
        const skillPoints = Number(rawSkillPoints);
        if (Number.isFinite(skillPoints) && skillPoints < 0) {
            console.warn(`[] ${skillPoints} 0`);
            warnings.push(`${skillPoints}`);
            set(mvuData.stat_data, '.$', 0);
            hasChanges = true;
            hasNegative = true;
        }
        if (Number.isFinite(skillPoints) && skillPoints > 500) {
            console.warn(`[] ${skillPoints} (> 500)`);
            warnings.push(`${skillPoints}>500`);
            set(mvuData.stat_data, '.$', 0);
            hasChanges = true;
        }
        // 
        if (hasChanges) {
            if (warnings.length > 0 && typeof toastr !== 'undefined') {
                const message = hasNegative
                    ? `\n${warnings.join('\n')}\n 0`
                    : `\n${warnings.join('\n')}\n`;
                toastr.warning(message, hasNegative ? '' : '', { timeOut: 8000 });
            }
            await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
            console.info('[] ');
        }
    }
    catch (error) {
        console.error('[] :', error);
    }
}
