/**
 *  MVU 
 */
async function waitForMvuReady(maxRetries = 20, interval = 500) {
    for (let i = 0; i < maxRetries; i++) {
        if (typeof Mvu !== 'undefined' && Mvu) {
            console.info(`[] MVU  ( ${i + 1} )`);
            return true;
        }
        await new Promise(resolve => setTimeout(resolve, interval));
    }
    console.error('[]  MVU ');
    return false;
}
