CA,aAAA,CAEF,iEACE,2BAAA,CACA,aAAA,CACA,sCAAA,CAIJ,8CACE,cAAA,CACA,wBAAA,CACA,eAAA,CACA,iBAAA,CACA,mBAAA,CACA,oBAAA,CACA,2BAAA,CACA,eAAA,CAGF,mDACE,YAAA,CACA,OAAA,CACA,cAAA,CAEA,gEACE,wBAAA,CAEF,gEACE,aAAA,CAKN,iCACE,YAAA,CACA,kBAAA,CACA,QAAA,CACA,YAAA,CACA,8BAAA,CACA,kBAAA,CACA,oCAAA,CAEA,mCACE,cAAA,CACA,wBAAA,CAGF,sCACE,cAAA,CACA,wBAAA,CAKJ,sCACE,eAAA,CACA,kBAAA,CACA,8BAAA,CACA,sCAAA,CACA,eAAA,CAEA,0DACE,YAAA,CACA,kBAAA,CACA,6BAAA,CACA,iBAAA,CACA,cAAA,CACA,yBAAA,CAEA,gEACE,8BAAA,CAGF,wEACE,aAAA,CACA,gBAAA,CAGF,+DACE,MAAA,CACA,cAAA,CACA,eAAA,CACA,wBAAA,CAIJ,2DACE,YAAA,CACA,wCAAA,CAGF,sDACE,YAAA,CACA,QAAA,CAEA,4DACE,MAAA,CACA,iBAAA,CACA,iBAAA,CACA,mCAAA,CACA,yBAAA,CACA,UAAA,CACA,cAAA,CAEA,yEACE,wBAAA,CAGF,kEACE,YAAA,CACA,gCAAA,CAIJ,kEACE,iBAAA,CACA,iBAAA,CACA,WAAA,CACA,kDAAA,CACA,UAAA,CACA,cAAA,CACA,eAAA,CACA,cAAA,CACA,kBAAA,CAEA,wEACE,0BAAA,CACA,0CAAA,CAMJ,8DACE,YAAA,CACA,kBAAA,CACA,OAAA,CACA,cAAA,CACA,eAAA,CACA,wBAAA,CACA,kBAAA,CAEA,gEACE,aAAA,CAKN,0DACE,gBAAA,CACA,eAAA,CACA,YAAA,CACA,qBAAA,CACA,OAAA,CAEA,6EACE,SAAA,CAGF,mFACE,6BAAA,CACA,iBAAA,CAIJ,0DACE,YAAA,CACA,6BAAA,CACA,kBAAA,CACA,iBAAA,CACA,iBAAA,CACA,8BAAA,CACA,oCAAA,CACA,cAAA,CACA,kBAAA,CAEA,gEACE,8BAAA,CACA,yBAAA,CAGF,uEACE,cAAA,CACA,UAAA,CAGF,yEACE,cAAA,CACA,eAAA,CACA,eAAA,CACA,iBAAA,CACA,6BAAA,CAGF,kFACE,aAAA,CAEF,kFACE,aAAA,CAEF,kFACE,aAAA,CAEF,kFACE,aAAA,CAEF,mFACE,aAAA,CAGF,qEACE,yEAAA,CACA,iCAAA,CACA,oCAAA,CAEA,kFACE,aAAA,CACA,sCAAA,CAEF,oFACE,aAAA,CACA,2BAAA,CACA,sCAAA,CAGF,2EACE,oCAAA","sourcesContent":["\n.skill-page {\n  padding: 16px 20px;\n  overflow-y: auto;\n  flex: 1;\n}\n\n.skill-header {\n  margin-bottom: 20px;\n}\n\n.skill-points-card {\n  display: flex;\n  align-items: center;\n  gap: 14px;\n  padding: 18px 20px;\n  background: linear-gradient(135deg, rgba(139, 92, 246, 0.25), rgba(139, 92, 246, 0.08));\n  border: 1px solid rgba(139, 92, 246, 0.4);\n  border-radius: 16px;\n  backdrop-filter: blur(10px);\n\n  > i {\n    font-size: 28px;\n    color: #a78bfa;\n  }\n\n  .points-info {\n    display: flex;\n    flex-direction: column;\n    gap: 2px;\n  }\n\n  .points-label {\n    font-size: 12px;\n    color: rgba(255, 255, 255, 0.6);\n    text-transform: uppercase;\n    letter-spacing: 0.5px;\n  }\n\n  .points-value {\n    font-size: 28px;\n    font-weight: 700;\n    color: #c4b5fd;\n  }\n}\n\n.skills-section {\n  margin-bottom: 24px;\n}\n\n.section-title {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  font-size: 14px;\n  font-weight: 600;\n  color: rgba(255, 255, 255, 0.8);\n  margin-bottom: 16px;\n\n  i {\n    color: #667eea;\n  }\n\n  .skill-count {\n    font-size: 12px;\n    font-weight: normal;\n    color: rgba(255, 255, 255, 0.4);\n  }\n}\n\n.empty-state {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: 40px 20px;\n  background: rgba(255, 255, 255, 0.02);\n  border-radius: 16px;\n  border: 1px dashed rgba(255, 255, 255, 0.1);\n\n  i {\n    font-size: 36px;\n    color: rgba(255, 255, 255, 0.2);\n    margin-bottom: 12px;\n  }\n\n  p {\n    font-size: 14px;\n    color: rgba(255, 255, 255, 0.4);\n    margin: 0;\n  }\n\n  .empty-hint {\n    font-size: 12px;\n    color: rgba(255, 255, 255, 0.25);\n    margin-top: 6px;\n  }\n}\n\n.skill-list {\n  display: flex;\n  flex-direction: column;\n  gap: 14px;\n}\n\n.skill-card {\n  padding: 16px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 14px;\n  border: 1px solid rgba(255, 255, 255, 0.08);\n  transition: all 0.2s;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.05);\n    transform: translateY(-2px);\n    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);\n  }\n\n  &.rarity-c {\n    border-left: 3px solid #9ca3af;\n  }\n  &.rarity-b {\n    border-left: 3px solid #60a5fa;\n  }\n  &.rarity-a {\n    border-left: 3px solid #a78bfa;\n  }\n  &.rarity-s {\n    border-left: 3px solid #fbbf24;\n  }\n  &.rarity-ss {\n    border-left: 3px solid #f472b6;\n  }\n}\n\n.skill-header-row {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 10px;\n}\n\n.skill-name-area {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n}\n\n.skill-name {\n  font-size: 16px;\n  font-weight: 700;\n  color: white;\n}\n\n.skill-rarity {\n  font-size: 10px;\n  font-weight: 600;\n  padding: 2px 6px;\n  border-radius: 4px;\n\n  &.rarity-c {\n    background: rgba(156, 163, 175, 0.3);\n    color: #d1d5db;\n  }\n  &.rarity-b {\n    background: rgba(96, 165, 250, 0.3);\n    color: #93c5fd;\n  }\n  &.rarity-a {\n    background: rgba(167, 139, 250, 0.3);\n    color: #c4b5fd;\n  }\n  &.rarity-s {\n    background: rgba(251, 191, 36, 0.3);\n    color: #fcd34d;\n  }\n  &.rarity-ss {\n    background: rgba(244, 114, 182, 0.3);\n    color: #f9a8d4;\n  }\n}\n\n.skill-level {\n  font-size: 13px;\n  font-weight: 600;\n  color: #667eea;\n  background: rgba(102, 126, 234, 0.2);\n  padding: 4px 10px;\n  border-radius: 8px;\n}\n\n.skill-description {\n  font-size: 13px;\n  color: rgba(255, 255, 255, 0.6);\n  line-height: 1.5;\n  margin: 0 0 12px 0;\n}\n\n.skill-stats {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 8px;\n  margin-bottom: 10px;\n}\n\n.stat-item {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  font-size: 11px;\n  padding: 4px 8px;\n  border-radius: 6px;\n\n  i {\n    font-size: 10px;\n  }\n\n  &.cost {\n    background: rgba(52, 211, 153, 0.2);\n    color: #6ee7b7;\n    border: 1px solid rgba(52, 211, 153, 0.3);\n  }\n\n  &.cooldown {\n    background: rgba(96, 165, 250, 0.2);\n    color: #93c5fd;\n    border: 1px solid rgba(96, 165, 250, 0.3);\n  }\n\n  &.accuracy {\n    background: rgba(251, 191, 36, 0.2);\n    color: #fcd34d;\n    border: 1px solid rgba(251, 191, 36, 0.3);\n  }\n}\n\n.skill-damage {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  font-size: 12px;\n  margin-bottom: 10px;\n  padding: 8px 10px;\n  background: rgba(239, 68, 68, 0.1);\n  border-radius: 8px;\n\n  .damage-label {\n    color: rgba(255, 255, 255, 0.5);\n  }\n\n  .damage-source {\n    color: #f87171;\n    font-weight: 500;\n  }\n\n  .damage-value {\n    color: #fcd34d;\n    font-weight: 600;\n  }\n}\n\n.skill-mechanics {\n  display: flex;\n  gap: 8px;\n  margin-bottom: 10px;\n}\n\n.mechanic-tag {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  font-size: 10px;\n  padding: 3px 8px;\n  border-radius: 4px;\n\n  i {\n    font-size: 9px;\n  }\n\n  &.ignore-def {\n    background: rgba(239, 68, 68, 0.2);\n    color: #f87171;\n    border: 1px solid rgba(239, 68, 68, 0.3);\n  }\n\n  &.hit-count {\n    background: rgba(251, 146, 60, 0.2);\n    color: #fdba74;\n    border: 1px solid rgba(251, 146, 60, 0.3);\n  }\n\n  &.crit-mod {\n    background: rgba(167, 139, 250, 0.2);\n    color: #c4b5fd;\n    border: 1px solid rgba(167, 139, 250, 0.3);\n  }\n}\n\n.skill-actions {\n  display: flex;\n  justify-content: flex-end;\n  gap: 8px;\n  padding-top: 10px;\n  border-top: 1px solid rgba(255, 255, 255, 0.05);\n}\n\n.upgrade-btn {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 8px 16px;\n  background: linear-gradient(135deg, #667eea, #764ba2);\n  border: none;\n  border-radius: 8px;\n  color: white;\n  font-size: 12px;\n  font-weight: 600;\n  cursor: pointer;\n  transition: all 0.2s;\n\n  &:hover:not(:disabled) {\n    transform: scale(1.05);\n    box-shadow: 0 4px 16px rgba(102, 126, 234, 0.4);\n  }\n\n  &:disabled {\n    opacity: 0.5;\n    cursor: not-allowed;\n  }\n}\n\n.forget-btn {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 8px 12px;\n  background: rgba(239, 68, 68, 0.2);\n  border: 1px solid rgba(239, 68, 68, 0.3);\n  border-radius: 8px;\n  color: #f87171;\n  font-size: 12px;\n  font-weight: 500;\n  cursor: pointer;\n  transition: all 0.2s;\n\n  &:hover {\n    background: rgba(239, 68, 68, 0.3);\n    transform: scale(1.05);\n  }\n}\n\n// \n.resource-header {\n  display: flex;\n  gap: 12px;\n  margin-bottom: 16px;\n}\n\n.resource-card {\n  flex: 1;\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  padding: 12px 14px;\n  border-radius: 12px;\n  backdrop-filter: blur(10px);\n\n  > i {\n    font-size: 20px;\n  }\n\n  .resource-info {\n    display: flex;\n    flex-direction: column;\n    gap: 2px;\n  }\n\n  .resource-label {\n    font-size: 10px;\n    color: rgba(255, 255, 255, 0.5);\n    text-transform: uppercase;\n  }\n\n  .resource-value {\n    font-size: 18px;\n    font-weight: 700;\n  }\n\n  &.skill-points {\n    background: linear-gradient(135deg, rgba(139, 92, 246, 0.25), rgba(139, 92, 246, 0.08));\n    border: 1px solid rgba(139, 92, 246, 0.3);\n\n    > i {\n      color: #a78bfa;\n    }\n    .resource-value {\n      color: #c4b5fd;\n    }\n  }\n\n  &.gold {\n    background: linear-gradient(135deg, rgba(251, 191, 36, 0.25), rgba(251, 191, 36, 0.08));\n    border: 1px solid rgba(251, 191, 36, 0.3);\n\n    > i {\n      color: #fbbf24;\n    }\n    .resource-value {\n      color: #fcd34d;\n    }\n  }\n}\n\n// \n.tab-bar {\n  display: flex;\n  gap: 8px;\n  margin-bottom: 16px;\n  padding: 4px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 12px;\n}\n\n.tab-btn {\n  flex: 1;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 6px;\n  padding: 10px 12px;\n  background: transparent;\n  border: none;\n  border-radius: 8px;\n  color: rgba(255, 255, 255, 0.5);\n  font-size: 12px;\n  font-weight: 500;\n  cursor: pointer;\n  transition: all 0.2s;\n\n  i {\n    font-size: 12px;\n  }\n\n  &:hover {\n    color: rgba(255, 255, 255, 0.8);\n    background: rgba(255, 255, 255, 0.05);\n  }\n\n  &.active {\n    color: white;\n    background: linear-gradient(135deg, rgba(139, 92, 246, 0.4), rgba(139, 92, 246, 0.2));\n  }\n}\n\n// \n.gacha-section {\n  padding: 16px 0;\n}\n\n.gacha-info {\n  padding: 16px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 12px;\n  margin-bottom: 16px;\n\n  h3 {\n    font-size: 14px;\n    font-weight: 600;\n    color: rgba(255, 255, 255, 0.8);\n    margin: 0 0 12px 0;\n\n    i {\n      margin-right: 6px;\n      color: #60a5fa;\n    }\n  }\n}\n\n.rate-list {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 8px;\n  margin-bottom: 10px;\n}\n\n.rate-item {\n  font-size: 11px;\n  padding: 4px 10px;\n  border-radius: 6px;\n  font-weight: 500;\n\n  &.rarity-c {\n    background: rgba(156, 163, 175, 0.2);\n    color: #d1d5db;\n  }\n  &.rarity-b {\n    background: rgba(96, 165, 250, 0.2);\n    color: #93c5fd;\n  }\n  &.rarity-a {\n    background: rgba(167, 139, 250, 0.2);\n    color: #c4b5fd;\n  }\n  &.rarity-s {\n    background: rgba(251, 191, 36, 0.2);\n    color: #fcd34d;\n  }\n  &.rarity-ss {\n    background: rgba(244, 114, 182, 0.2);\n    color: #f9a8d4;\n  }\n  &.rarity-sin {\n    background: linear-gradient(135deg, rgba(20, 20, 20, 0.9), rgba(60, 20, 40, 0.8));\n    color: #dc2626;\n    border: 1px solid rgba(139, 0, 0, 0.5);\n    text-shadow: 0 0 6px rgba(220, 38, 38, 0.6);\n    animation: sin-rate-pulse 2s ease-in-out infinite;\n  }\n}\n\n@keyframes sin-rate-pulse {\n  0%,\n  100% {\n    box-shadow: 0 0 8px rgba(139, 0, 0, 0.3);\n  }\n  50% {\n    box-shadow: 0 0 15px rgba(139, 0, 0, 0.6);\n  }\n}\n\n.gacha-note {\n  font-size: 11px;\n  color: rgba(255, 255, 255, 0.4);\n  margin: 0;\n}\n\n.gacha-buttons {\n  display: flex;\n  gap: 12px;\n  margin-bottom: 20px;\n}\n\n.gacha-btn {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 8px;\n  padding: 20px 16px;\n  border: none;\n  border-radius: 14px;\n  cursor: pointer;\n  transition: all 0.2s;\n\n  i {\n    font-size: 28px;\n  }\n\n  .btn-text {\n    font-size: 14px;\n    font-weight: 600;\n  }\n\n  .btn-cost {\n    font-size: 11px;\n    opacity: 0.7;\n    display: flex;\n    gap: 4px;\n    align-items: center;\n\n    .discount-price {\n      color: #fbbf24;\n      font-weight: 600;\n      font-size: 12px;\n    }\n\n    .original-price {\n      text-decoration: line-through;\n      opacity: 0.5;\n      font-size: 10px;\n    }\n  }\n\n  &.single {\n    background: linear-gradient(135de