ay: flex;\n  align-items: center;\n  gap: 8px;\n  font-size: 13px;\n  font-weight: 600;\n  color: rgba(255, 255, 255, 0.8);\n  margin-bottom: 14px;\n\n  i:first-child {\n    color: #667eea;\n  }\n\n  .count-badge {\n    margin-left: auto;\n    padding: 2px 8px;\n    background: rgba(255, 255, 255, 0.1);\n    border-radius: 10px;\n    font-size: 11px;\n    font-weight: 500;\n    color: rgba(255, 255, 255, 0.6);\n  }\n}\n\n.present-list {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 10px;\n}\n\n.present-item {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  padding: 10px 14px;\n  background: linear-gradient(135deg, rgba(102, 126, 234, 0.15), rgba(102, 126, 234, 0.05));\n  border: 1px solid rgba(102, 126, 234, 0.2);\n  border-radius: 12px;\n\n  .present-avatar {\n    width: 32px;\n    height: 32px;\n    border-radius: 50%;\n    background: rgba(102, 126, 234, 0.3);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    overflow: hidden;\n    cursor: pointer;\n    transition: transform 0.2s ease;\n\n    &:hover {\n      transform: scale(1.1);\n    }\n\n    .avatar-img {\n      width: 100%;\n      height: 100%;\n      object-fit: cover;\n    }\n\n    i,\n    .fallback-icon {\n      font-size: 14px;\n      color: #a5b4fc;\n    }\n  }\n\n  .present-name {\n    font-size: 13px;\n    font-weight: 500;\n    color: white;\n  }\n}\n\n.relationship-list {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n.relationship-card {\n  padding: 16px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 16px;\n  border: 1px solid rgba(255, 255, 255, 0.08);\n  position: relative;\n}\n\n.discard-btn {\n  width: 18px;\n  height: 18px;\n  border-radius: 999px;\n  background: rgba(248, 113, 113, 0.9);\n  border: 1px solid rgba(248, 113, 113, 0.9);\n  color: white;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.2s ease;\n  position: absolute;\n  top: 12px;\n  right: 12px;\n  z-index: 2;\n\n  &:hover {\n    transform: scale(1.08);\n    background: rgba(248, 113, 113, 1);\n  }\n\n  &:active {\n    transform: scale(1);\n  }\n\n  i {\n    font-size: 10px;\n    line-height: 1;\n  }\n}\n\n.rel-header {\n  display: flex;\n  align-items: center;\n  gap: 14px;\n  margin-bottom: 16px;\n}\n\n.rel-avatar {\n  width: 48px;\n  height: 48px;\n  border-radius: 50%;\n  background: linear-gradient(135deg, rgba(102, 126, 234, 0.3), rgba(118, 75, 162, 0.2));\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  overflow: hidden;\n  cursor: pointer;\n  transition: transform 0.2s ease;\n\n  &:hover {\n    transform: scale(1.1);\n  }\n\n  .avatar-img {\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n  }\n\n  i,\n  .fallback-icon {\n    font-size: 20px;\n    color: rgba(255, 255, 255, 0.6);\n  }\n}\n\n.rel-info {\n  flex: 1;\n}\n\n.rel-name {\n  font-size: 16px;\n  font-weight: 600;\n  color: white;\n  margin-bottom: 6px;\n}\n\n.rel-type {\n  display: inline-flex;\n  align-items: center;\n  gap: 6px;\n  padding: 4px 10px;\n  border-radius: 12px;\n  font-size: 11px;\n  font-weight: 500;\n\n  i {\n    font-size: 10px;\n  }\n\n  &.type-stranger {\n    background: rgba(156, 163, 175, 0.15);\n    color: #d1d5db;\n  }\n\n  &.type-classmate {\n    background: rgba(96, 165, 250, 0.15);\n    color: #93c5fd;\n  }\n\n  &.type-friend {\n    background: rgba(52, 211, 153, 0.15);\n    color: #6ee7b7;\n  }\n\n  &.type-lover {\n    background: rgba(244, 114, 182, 0.15);\n    color: #f9a8d4;\n  }\n\n  &.type-master {\n    background: rgba(251, 191, 36, 0.15);\n    color: #fcd34d;\n  }\n\n  &.type-submissive {\n    background: rgba(167, 139, 250, 0.15);\n    color: #c4b5fd;\n  }\n\n  &.type-enemy {\n    background: rgba(248, 113, 113, 0.15);\n    color: #fca5a5;\n  }\n\n  &.type-unknown {\n    background: rgba(156, 163, 175, 0.1);\n    color: rgba(255, 255, 255, 0.4);\n  }\n}\n\n.rel-stats {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n.stat-item {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.stat-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.stat-label {\n  font-size: 11px;\n  color: rgba(255, 255, 255, 0.5);\n}\n\n.stat-value {\n  font-size: 13px;\n  font-weight: 600;\n  font-family: 'JetBrains Mono', monospace;\n\n  &.very-high {\n    color: #f472b6;\n  }\n  &.high {\n    color: #34d399;\n  }\n  &.medium {\n    color: #60a5fa;\n  }\n  &.low {\n    color: #fbbf24;\n  }\n  &.very-low {\n    color: rgba(255, 255, 255, 0.4);\n  }\n  &.training {\n    color: #a78bfa;\n  }\n}\n\n.stat-bar {\n  height: 6px;\n  background: rgba(255, 255, 255, 0.1);\n  border-radius: 3px;\n  overflow: hidden;\n}\n\n.stat-fill {\n  height: 100%;\n  border-radius: 3px;\n  transition: width 0.4s ease;\n\n  &.affection {\n    &.very-high {\n      background: linear-gradient(90deg, #ec4899, #f472b6);\n    }\n    &.high {\n      background: linear-gradient(90deg, #10b981, #34d399);\n    }\n    &.medium {\n      background: linear-gradient(90deg, #3b82f6, #60a5fa);\n    }\n    &.low {\n      background: linear-gradient(90deg, #f59e0b, #fbbf24);\n    }\n    &.very-low {\n      background: rgba(255, 255, 255, 0.2);\n    }\n  }\n\n  &.training {\n    background: linear-gradient(90deg, #8b5cf6, #a78bfa);\n  }\n\n  &.submission {\n    &.very-high {\n      background: linear-gradient(90deg, #ec4899, #f472b6);\n    }\n    &.high {\n      background: linear-gradient(90deg, #f59e0b, #fbbf24);\n    }\n    &.medium {\n      background: linear-gradient(90deg, #3b82f6, #60a5fa);\n    }\n    &.low {\n      background: linear-gradient(90deg, #10b981, #34d399);\n    }\n    &.very-low {\n      background: rgba(255, 255, 255, 0.2);\n    }\n  }\n}\n\n.empty-state {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: 60px 20px;\n  text-align: center;\n\n  .empty-icon {\n    width: 80px;\n    height: 80px;\n    border-radius: 50%;\n    background: rgba(255, 255, 255, 0.03);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    margin-bottom: 20px;\n\n    i {\n      font-size: 32px;\n      color: rgba(255, 255, 255, 0.15);\n    }\n  }\n\n  .empty-title {\n    font-size: 16px;\n    font-weight: 600;\n    color: rgba(255, 255, 255, 0.5);\n    margin-bottom: 6px;\n  }\n\n  .empty-desc {\n    font-size: 13px;\n    color: rgba(255, 255, 255, 0.3);\n  }\n}\n\n.reputation-list {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\n.reputation-card {\n  padding: 12px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 12px;\n  border: 1px solid rgba(255, 255, 255, 0.08);\n}\n\n.rep-header {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  margin-bottom: 8px;\n}\n\n.rep-icon {\n  width: 36px;\n  height: 36px;\n  border-radius: 50%;\n  background: linear-gradient(135deg, rgba(102, 126, 234, 0.3), rgba(118, 75, 162, 0.2));\n  display: flex;\n  align-items: center;\n  justify-content: center;\n\n  i {\n    font-size: 16px;\n    color: rgba(255, 255, 255, 0.7);\n  }\n}\n\n.rep-info {\n  flex: 1;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.rep-name {\n  font-size: 14px;\n  font-weight: 500;\n  color: white;\n}\n\n.rep-value {\n  font-size: 14px;\n  font-weight: 600;\n  font-family: 'JetBrains Mono', monospace;\n\n  &.very-high {\n    color: #34d399;\n  }\n  &.high {\n    color: #60a5fa;\n  }\n  &.medium {\n    color: rgba(255, 255, 255, 0.6);\n  }\n  &.low {\n    color: #fbbf24;\n  }\n  &.very-low {\n    color: #f87171;\n  }\n}\n\n.rep-bar {\n  height: 4px;\n  background: rgba(255, 255, 255, 0.1);\n  border-radius: 2px;\n  overflow: hidden;\n}\n\n.rep-fill {\n  height: 100%;\n  border-radius: 2px;\n  transition: width 0.4s ease;\n\n  &.very-high {\n    background: linear-gradient(90deg, #10b981, #34d399);\n  }\n  &.high {\n    background: linear-gradient(90deg, #3b82f6, #60a5fa);\n  }\n  &.medium {\n    background: rgba(255, 255, 255, 0.3);\n  }\n  &.low {\n    background: linear-gradient(90deg, #f59e0b, #fbbf24);\n  }\n  &.very-low {\n    background: linear-gradient(90deg, #ef4444, #f87171);\n  }\n}\n\n// \n.avatar-modal {\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  z-index: 99999;\n  display: flex;\n  align-items: flex-start;\n  justify-content: center;\n  padding: 16px;\n  overflow-y: auto;\n  -webkit-overflow-scrolling: touch;\n}\n\n.modal-backdrop {\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background: rgba(0, 0, 0, 0.85);\n  backdrop-filter: blur(8px);\n}\n\n.modal-content {\n  position: relative;\n  background: linear-gradient(135deg, rgba(30, 30, 50, 0.98), rgba(20, 20, 40, 0.98));\n  border-radius: 20px;\n  border: 1px solid rgba(255, 255, 255, 0.15);\n  width: 360px;\n  max-width: 95vw;\n  max-height: 100%;\n  overflow: hidden;\n  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.6);\n  margin-top: 8px;\n  margin-bottom: 20px;\n  display: flex;\n  flex-direction: column;\n\n  @media (min-height: 600px) {\n    margin-top: max(20px, 5vh);\n  }\n}\n\n.modal-close {\n  position: absolute;\n  top: 16px;\n  right: 16px;\n  width: 32px;\n  height: 32px;\n  border-radius: 50%;\n  background: rgba(248, 113, 113, 0.9);\n  border: none;\n  color: white;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  z-index: 10;\n  transition: all 0.2s ease;\n\n  &:hover {\n    transform: scale(1.1);\n    background: rgba(248, 113, 113, 1);\n  }\n\n  i {\n    font-size: 14px;\n  }\n}\n\n.modal-header {\n  padding: 20px 24px;\n  border-bottom: 1px solid rgba(255, 255, 255, 0.1);\n\n  h3 {\n    margin: 0;\n    font-size: 18px;\n    font-weight: 600;\n    color: white;\n  }\n}\n\n.modal-body {\n  padding: 24px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  min-height: 200px;\n  flex: 1;\n  min-height: 0;\n}\n\n.modal-avatar-img {\n  width: 100%;\n  max-height: 100%;\n  border-radius: 12px;\n  object-fit: contain;\n}\n\n.modal-fallback {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 12px;\n  color: rgba(255, 255, 255, 0.4);\n\n  i {\n    font-size: 48px;\n  }\n\n  p {\n    margin: 0;\n    font-size: 14px;\n  }\n}\n"],"sourceRoot":""}]);
