id rgba(255, 255, 255, 0.2);\n  border-radius: 8px;\n  color: white;\n  font-size: 12px;\n  cursor: pointer;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.15);\n  }\n}\n\n.zoom-btn {\n  width: 28px;\n  height: 28px;\n  border-radius: 6px;\n  background: rgba(255, 255, 255, 0.1);\n  border: 1px solid rgba(255, 255, 255, 0.2);\n  color: white;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n  transition: all 0.2s;\n\n  &:hover:not(:disabled) {\n    background: rgba(255, 255, 255, 0.2);\n  }\n\n  &:disabled {\n    opacity: 0.3;\n    cursor: not-allowed;\n  }\n\n  i {\n    font-size: 12px;\n  }\n}\n\n.zoom-level {\n  min-width: 50px;\n  text-align: center;\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0.6);\n}\n\n.map-container {\n  position: relative;\n  overflow: auto;\n  background: rgba(0, 0, 0, 0.2);\n  border-radius: 12px;\n  border: 1px solid rgba(255, 255, 255, 0.1);\n  padding: 20px;\n  min-height: 400px;\n}\n\n.map-grid {\n  display: inline-block;\n  transition: transform 0.2s ease;\n}\n\n.map-row {\n  display: flex;\n}\n\n.map-cell {\n  width: 80px;\n  height: 80px;\n  border: 1px solid rgba(255, 255, 255, 0.1);\n  background: rgba(255, 255, 255, 0.02);\n  position: relative;\n  cursor: pointer;\n  transition: all 0.2s;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.05);\n    border-color: rgba(102, 126, 234, 0.5);\n  }\n\n  &.has-location {\n    background: rgba(102, 126, 234, 0.1);\n    border-color: rgba(102, 126, 234, 0.3);\n  }\n\n  &.current-position {\n    background: rgba(34, 211, 238, 0.2);\n    border-color: rgba(34, 211, 238, 0.5);\n    box-shadow: 0 0 10px rgba(34, 211, 238, 0.3);\n  }\n\n  // \n  &.empty-cell {\n    border: none;\n    background: transparent;\n    cursor: default;\n\n    &:hover {\n      background: transparent;\n      border: none;\n    }\n  }\n}\n\n.cell-coord {\n  position: absolute;\n  top: 2px;\n  left: 2px;\n  font-size: 8px;\n  color: rgba(255, 255, 255, 0.3);\n  font-family: 'JetBrains Mono', monospace;\n}\n\n.location-marker {\n  width: 100%;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  gap: 4px;\n  padding: 4px;\n\n  i {\n    font-size: 20px;\n    color: #a5b4fc;\n  }\n\n  .location-label {\n    font-size: 9px;\n    color: rgba(255, 255, 255, 0.8);\n    text-align: center;\n    line-height: 1.2;\n    max-width: 100%;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n  }\n\n  &.faction-student-council i {\n    color: #fbbf24;\n  }\n  &.faction-feminist i {\n    color: #ec4899;\n  }\n  &.faction-bf i {\n    color: #8b5cf6;\n  }\n  &.faction-sports i {\n    color: #10b981;\n  }\n  &.faction-research i {\n    color: #3b82f6;\n  }\n  &.faction-underground i {\n    color: #6b7280;\n  }\n  &.faction-male-alliance i {\n    color: #60a5fa;\n  }\n  &.faction-femdom i {\n    color: #f472b6;\n  }\n  &.faction-public i {\n    color: #a5b4fc;\n  }\n  &.faction-independent i {\n    color: #fcd34d;\n  }\n  &.faction-admin i {\n    color: #f59e0b;\n  }\n  &.faction-none i {\n    color: rgba(255, 255, 255, 0.3);\n  }\n}\n\n.location-detail {\n  position: fixed;\n  bottom: 100px;\n  right: 20px;\n  width: 280px;\n  background: rgba(15, 23, 42, 0.95);\n  border: 1px solid rgba(255, 255, 255, 0.2);\n  border-radius: 16px;\n  padding: 16px;\n  backdrop-filter: blur(10px);\n  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);\n  z-index: 100;\n}\n\n.detail-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 12px;\n\n  h3 {\n    font-size: 16px;\n    font-weight: 600;\n    color: white;\n    margin: 0;\n  }\n}\n\n.close-detail {\n  width: 24px;\n  height: 24px;\n  border-radius: 50%;\n  background: rgba(255, 255, 255, 0.1);\n  border: none;\n  color: white;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n  transition: all 0.2s;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.2);\n  }\n\n  i {\n    font-size: 12px;\n  }\n}\n\n.detail-content {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.detail-item {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 8px 0;\n  border-bottom: 1px solid rgba(255, 255, 255, 0.1);\n\n  &:last-child {\n    border-bottom: none;\n  }\n}\n\n.detail-label {\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0.6);\n}\n\n.detail-value {\n  font-size: 12px;\n  font-weight: 500;\n  color: white;\n}\n\n.current-location {\n  position: fixed;\n  bottom: 20px;\n  left: 50%;\n  transform: translateX(-50%);\n  padding: 10px 20px;\n  background: rgba(34, 211, 238, 0.2);\n  border: 1px solid rgba(34, 211, 238, 0.5);\n  border-radius: 20px;\n  color: white;\n  font-size: 12px;\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  backdrop-filter: blur(10px);\n  z-index: 100;\n\n  i {\n    color: #22d3ee;\n  }\n}\n"],"sourceRoot":""}]);
