AAA,CACA,eAAA,CACA,kDAAA,CACA,kBAAA,CACA,mCAAA,CACA,eAAA,CAGF,+BACE,YAAA,CACA,6BAAA,CACA,kBAAA,CACA,iBAAA,CACA,2CAAA,CAEA,kCACE,cAAA,CACA,eAAA,CACA,UAAA,CACA,QAAA,CAGF,0CACE,UAAA,CACA,WAAA,CACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,8BAAA,CACA,WAAA,CACA,iBAAA,CACA,wBAAA,CACA,cAAA,CAEA,gDACE,6BAAA,CACA,UAAA,CAKN,6BACE,YAAA,CAGF,wCACE,YAAA,CACA,kBAAA,CACA,QAAA,CACA,YAAA,CACA,8BAAA,CACA,kBAAA,CACA,kBAAA,CAGF,+BACE,UAAA,CACA,WAAA,CACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,8BAAA,CACA,kBAAA,CAEA,iCACE,cAAA,CACA,wBAAA,CAGF,uCACE,wBAAA,CAEF,uCACE,wBAAA,CAEF,uCACE,wBAAA,CAEF,uCACE,wBAAA,CAEF,wCACE,wBAAA,CAIJ,+BACE,MAAA,CACA,YAAA,CACA,qBAAA,CACA,OAAA,CAGF,+BACE,cAAA,CACA,eAAA,CACA,UAAA,CAGF,+BACE,cAAA,CACA,wBAAA,CACA,eAAA,CAGF,+BACE,kBAAA,CACA,YAAA,CACA,8BAAA,CACA,kBAAA,CAGF,+BACE,cAAA,CACA,wBAAA,CACA,iBAAA,CAGF,6BACE,YAAA,CACA,cAAA,CACA,OAAA,CAGF,6BACE,YAAA,CACA,kBAAA,CACA,OAAA,CACA,cAAA,CACA,eAAA,CACA,8BAAA,CACA,iBAAA,CAGF,6BACE,wBAAA,CAGF,8BACE,eAAA,CAEA,uCACE,aAAA,CAEF,uCACE,aAAA,CAIJ,oCACE,YAAA,CACA,qBAAA,CACA,kBAAA,CACA,QAAA,CACA,kBAAA,CAEA,+CACE,wBAAA,CACA,cAAA,CAGF,sDACE,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,QAAA,CAEA,4DACE,UAAA,CACA,YAAA,CACA,8BAAA,CACA,mCAAA,CACA,iBAAA,CACA,UAAA,CACA,cAAA,CACA,eAAA,CACA,iBAAA,CAEA,kEACE,YAAA,CACA,gCAAA,CAIF,8KAEE,uBAAA,CACA,QAAA,CApBJ,4DAsBE,yBAAA,CAIJ,6CACE,UAAA,CACA,WAAA,CACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,6BAAA,CACA,oCAAA,CACA,iBAAA,CACA,UAAA,CACA,cAAA,CACA,cAAA,CACA,kBAAA,CAEA,mDACE,6BAAA,CAIJ,8CACE,cAAA,CACA,wBAAA,CAEA,2DACE,aAAA,CACA,eAAA,CAIJ,yGAEE,UAAA,CACA,kBAAA,CAIJ,gCACE,YAAA,CACA,6BAAA,CACA,kBAAA,CACA,iBAAA,CACA,8BAAA,CACA,kBAAA,CAEA,qCACE,wBAAA,CACA,cAAA,CAGF,6CACE,YAAA,CACA,kBAAA,CACA,OAAA,CACA,cAAA,CACA,eAAA,CACA,aAAA,CAEA,+CACE,cAAA,CAKN,+BACE,YAAA,CACA,QAAA,CACA,iBAAA,CACA,wCAAA,CAGF,2DAEE,MAAA,CACA,YAAA,CACA,kBAAA,CACA,cAAA,CACA,eAAA,CACA,cAAA,CACA,kBAAA,CAGF,6BACE,8BAAA,CACA,oCAAA,CACA,wBAAA,CAEA,mCACE,8BAAA,CACA,UAAA,CAIJ,8BACE,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,OAAA,CACA,kDAAA,CACA,WAAA,CACA,UAAA,CAEA,mDACE,qBAAA,CACA,0CAAA,CAGF,uCACE,UAAA,CACA,kBAAA,CAIJ,mCACE,aAAA,CAEA,qCACE,aAAA","sourcesContent":["\n.shop-page {\n  position: relative;\n  padding: 16px 20px;\n  overflow-y: auto;\n  flex: 1;\n}\n\n.shop-header {\n  margin-bottom: 16px;\n  position: relative;\n}\n\n.special-battle-unlock {\n  position: absolute;\n  top: 0;\n  right: 0;\n  width: 20px;\n  height: 20px;\n  border: none;\n  background: transparent;\n  opacity: 0.08;\n  cursor: pointer;\n  transition: opacity 0.2s;\n}\n\n.special-battle-unlock:hover {\n  opacity: 0.2;\n}\n\n.gold-card {\n  display: flex;\n  align-items: center;\n  gap: 14px;\n  padding: 16px 20px;\n  background: linear-gradient(135deg, rgba(251, 191, 36, 0.25), rgba(251, 191, 36, 0.08));\n  border: 1px solid rgba(251, 191, 36, 0.4);\n  border-radius: 16px;\n  position: relative;\n  backdrop-filter: blur(10px);\n\n  > i {\n    font-size: 28px;\n    color: #fbbf24;\n  }\n\n  .gold-info {\n    display: flex;\n    flex-direction: column;\n    gap: 2px;\n  }\n\n  .gold-label {\n    font-size: 12px;\n    color: rgba(255, 255, 255, 0.6);\n  }\n\n  .gold-value {\n    font-size: 26px;\n    font-weight: 700;\n    color: #fcd34d;\n  }\n}\n\n.category-tabs {\n  display: flex;\n  gap: 8px;\n  margin-bottom: 16px;\n  overflow-x: auto;\n  padding-bottom: 4px;\n\n  &::-webkit-scrollbar {\n    height: 3px;\n  }\n}\n\n.category-tab {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 10px 16px;\n  background: rgba(255, 255, 255, 0.05);\n  border: 1px solid rgba(255, 255, 255, 0.1);\n  border-radius: 10px;\n  color: rgba(255, 255, 255, 0.6);\n  font-size: 13px;\n  font-weight: 500;\n  cursor: pointer;\n  transition: all 0.2s;\n  white-space: nowrap;\n\n  i {\n    font-size: 14px;\n  }\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.08);\n    color: rgba(255, 255, 255, 0.8);\n  }\n\n  &.active {\n    background: linear-gradient(135deg, #667eea, #764ba2);\n    border-color: transparent;\n    color: white;\n  }\n}\n\n.filter-bar {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  padding: 12px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 12px;\n  margin-bottom: 16px;\n}\n\n.filter-group {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  flex-wrap: wrap;\n}\n\n.filter-label {\n  font-size: 11px;\n  color: rgba(255, 255, 255, 0.5);\n  min-width: 36px;\n}\n\n.filter-options {\n  display: flex;\n  gap: 6px;\n  flex-wrap: wrap;\n}\n\n.filter-btn {\n  padding: 4px 10px;\n  font-size: 11px;\n  background: rgba(255, 255, 255, 0.05);\n  border: 1px solid rgba(255, 255, 255, 0.1);\n  border-radius: 6px;\n  color: rgba(255, 255, 255, 0.6);\n  cursor: pointer;\n  transition: all 0.2s;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.1);\n    color: white;\n  }\n\n  &.active {\n    color: white;\n\n    &.grade-c {\n      background: rgba(156, 163, 175, 0.4);\n      border-color: #9ca3af;\n    }\n    &.grade-b {\n      background: rgba(96, 165, 250, 0.4);\n      border-color: #60a5fa;\n    }\n    &.grade-a {\n      background: rgba(167, 139, 250, 0.4);\n      border-color: #a78bfa;\n    }\n    &.grade-s {\n      background: rgba(251, 191, 36, 0.4);\n      border-color: #fbbf24;\n    }\n    &.grade-ss {\n      background: rgba(244, 114, 182, 0.4);\n      border-color: #f472b6;\n    }\n  }\n\n  &.slot-btn.active {\n    background: rgba(102, 126, 234, 0.4);\n    border-color: #667eea;\n  }\n}\n\n.empty-filter {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 8px;\n  padding: 30px;\n  color: rgba(255, 255, 255, 0.4);\n\n  i {\n    font-size: 24px;\n  }\n  span {\n    font-size: 13px;\n  }\n}\n\n.shop-content {\n  min-height: 200px;\n}\n\n.item-section {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n.sub-category {\n  margin-bottom: 8px;\n}\n\n.item-meta {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n}\n\n.item-grade {\n  font-size: 10px;\n  padding: 1px 5px;\n  border-radius: 3px;\n  font-weight: 600;\n\n  &.grade-c {\n    background: rgba(156, 163, 175, 0.3);\n    color: #d1d5db;\n  }\n  &.grade-b {\n    background: rgba(96, 165, 250, 0.3);\n    color: #93c5fd;\n  }\n  &.grade-a {\n    background: rgba(167, 139, 250, 0.3);\n    color: #c4b5fd;\n  }\n  &.grade-s {\n    background: rgba(251, 191, 36, 0.3);\n    color: #fcd34d;\n  }\n  &.grade-ss {\n    background: rgba(244, 114, 182, 0.3);\n    color: #f9a8d4;\n  }\n}\n\n.sub-title {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  font-size: 13px;\n  font-weight: 600;\n  color: rgba(255, 255, 255, 0.7);\n  margin-bottom: 12px;\n\n  i {\n    font-size: 14px;\n  }\n}\n\n.grade-badge,\n.tier-badge {\n  font-size: 11px;\n  padding: 2px 8px;\n  border-radius: 4px;\n  font-weight: 600;\n}\n\n.grade-c .grade-badge {\n  background: rgba(156, 163, 175, 0.3);\n  color: #d1d5db;\n}\n.grade-b .grade-badge {\n  background: rgba(96, 165, 250, 0.3);\n  color: #93c5fd;\n}\n.grade-a .grade-badge {\n  background: rgba(167, 139, 250, 0.3);\n  color: #c4b5fd;\n}\n.grade-s .grade-badge {\n  background: rgba(251, 191, 36, 0.3);\n  color: #fcd34d;\n}\n.grade-ss .grade-badge {\n  background: rgba(244, 114, 182, 0.3);\n  color: #f9a8d4;\n}\n\n.tier-basic .tier-badge {\n  background: rgba(156, 163, 175, 0.3);\n  color: #d1d5db;\n}\n.tier-fine .tier-badge {\n  background: rgba(96, 165, 250, 0.3);\n  color: #93c5fd;\n}\n.tier-precious .tier-badge {\n  background: rgba(167, 139, 250, 0.3);\n  color: #c4b5fd;\n}\n.tier-special .tier-badge {\n  background: rgba(251, 191, 36, 0.3);\n  color: #fcd34d;\n}\n\n.item-grid {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.shop-item {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  padding: 12px 14px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 12px;\n  border: 1px solid rgba(255, 255, 255, 0.08);\n  cursor: pointer;\n  transition: all 0.2s;\n  position: relative;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.06);\n    transform: translateX(4px);\n  }\n\n  &.grade-c {\n    border-left: 3px solid #9ca3af;\n  }\n  &.grade-b {\n    border-left: 3px solid #60a5fa;\n  }\n  &.grade-a {\n    border-left: 3px solid #a78bfa;\n  }\n  &.grade-s {\n    border-left: 3px solid #fbbf24;\n  }\n  &.grade-ss {\n    border-left: 3px solid #f472b6;\n  }\n\n  &.tier-basic {\n    border-left: 3px solid #9ca3af;\n  }\n  &.tier-fine {\n    border-left: 3px solid #60a5fa;\n  }\n  &.tier-precious {\n    border-left: 3px solid #a78bfa;\n  }\n  &.tier-special {\n    border-left: 3px solid #fbbf24;\n  }\n}\n\n.item-icon {\n  width: 40px;\n  height: 40px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background: rgba(255, 255, 255, 0.05);\n  border-radius: 10px;\n\n  i {\n    font-size: 18px;\n    color: rgba(255, 255, 255, 0.7);\n  }\n}\n\n.item-info {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  gap: 2px;\n  min-width: 0;\n}\n\n.item-name {\n  font-size: 13px;\n  font-weight: 600;\n  color: white;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.item-type,\n.item-effect {\n  font-size: 11px;\n  color: rgba(255, 255, 255, 0.5);\n}\n\n.item-price {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  font-size: 13px;\n  font-weight: 600;\n  color: #fcd34d;\n\n  i {\n    font-size: 11px;\n  }\n\n  .original-price {\n    text-decoration: line-through;\n    opacity: 0.5;\n    font-size: 11px;\n    color: #9ca3af;\n  }\n\n  .discounted-price {\n    color: #4ade80;\n    font-weight: 700;\n  }\n\n  .discount-tag {\n    background: linear-gradient(135deg, #22c55e, #16a34a);\n    color: white;\n    font-size: 9px;\n    padding: 2px 5px;\n    border-radius: 4px;\n    font-weight: 700;\n    margin-left: 4px;\n  }\n}\n\n.combat-tag {\n  position: absolute;\n  top: 6px;\n  right: 6px;\n  font-size: 9px;\n  padding: 2px 6px;\n  background: rgba(239, 68, 68, 0.3);\n  color: #f87171;\n  border-radius: 4px;\n}\n\n/*  */\n.purchase-modal {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background: rgba(0, 0, 0, 0.7);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  z-index: 1000;\n  padding: 20px;\n}\n\n.modal-content {\n  width: 100%;\n  max-width: 340px;\n  background: linear-gradient(180deg, #1e1e2e, #151520);\n  border-radius: 20px;\n  border: 1px solid rgba(255, 255, 255, 0.1);\n  overflow: hidden;\n}\n\n.modal-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 16px 20px;\n  border-bottom: 1px solid rgba(255, 255, 255, 0.08);\n\n  h3 {\n    font-size: 16px;\n    font-weight: 600;\n    color: white;\n    margin: 0;\n  }\n\n  .close-btn {\n    width: 32px;\n    height: 32px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background: rgba(255, 255, 255, 0.05);\n    border: none;\n    border-radius: 8px;\n    color: rgba(255, 255, 255, 0.6);\n    cursor: pointer;\n\n    &:hover {\n      background: rgba(255, 255, 255, 0.1);\n      color: white;\n    }\n  }\n}\n\n.modal-body {\n  padding: 20px;\n}\n\n.selected-item-preview {\n  display: flex;\n  align-items: center;\n  gap: 14px;\n  padding: 14px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 12px;\n  margin-bottom: 16px;\n}\n\n.preview-icon {\n  width: 50px;\n  height: 50px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background: rgba(255, 255, 255, 0.05);\n  border-radius: 12px;\n\n  i {\n    font-size: 22px;\n    color: rgba(255, 255, 255, 0.8);\n  }\n\n  &.grade-c {\n    border: 2px solid #9ca3af;\n  }\n  &.grade-b {\n    border: 2px solid #60a5fa;\n  }\n  &.grade-a {\n    border: 2px solid #a78bfa;\n  }\n  &.grade-s {\n    border: 2px solid #fbbf24;\n  }\n  &.grade-ss {\n    border: 2px solid #f472b6;\n  }\n}\n\n.preview-info {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n}\n\n.preview-name {\n  font-size: 15px;\n  font-weight: 600;\n  color: white;\n}\n\n.preview-desc {\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0