export type TalentEffectType =
  | 'pleasure_control' // 
  | 'stamina_control' // 
  | 'damage_boost' // 
  | 'defense_boost' // 
  | 'first_strike' // 
  | 'counter' // 
  | 'recovery' // 
  | 'special' // 
  | 'daily' // 
  | 'sin'; // 

export interface TalentEffect {
  type: TalentEffectType;
  trigger: string; // 
  effect: string; // 
  // 
  params: {
    threshold?: number; // 5050%
    value?: number; // 
    duration?: number; // 
    count?: number; // 
    chance?: number; // 
    multiplier?: number; // 
    maxValue?: number; // 
  };
}

export interface TalentData {
  id: string;
  name: string;
  description: string; // 
  rarity: 'C' | 'B' | 'A' | 'S' | 'SS' | 'SIN';
  bonus: TalentBonus; // 
  effects: TalentEffect[]; // 
}

