/**
 * 
 * 
 * MVU
 * C: buff
 * B: buff
 * A: buff/debuff
 * S: buff/debuff
 * SS: buff/debuff
 */

export interface GachaSkillData {
  id: string;
  name: string;
  description: string;
  effectDescription: string;
  icon: string;
  type: string;
  rarity: 'C' | 'B' | 'A' | 'S' | 'SS';
  staminaCost: number;
  cooldown: number;
  damageSource: string;
  coefficient: number;
  accuracy: number;
  critModifier: number;
  hitCount: number;
  buffs: Array<{
    type: '' | '' | '' | '' | '' | '' | '';
    value: number;
    isPercent: boolean;
    duration: number;
    isTargetEnemy: boolean; // true=, false=
  }>;
}

